import { css, styled } from '../../utils/styled';
import { color, fontFamily, modifier, spacing, typography, variants } from '../../utils/theme';
import type { PressableProps } from '../Pressable';
import { Pressable } from '../Pressable';
import type { Alignment, Status } from './TopBanner';

type ContainerProps = {
    styleStatus: Status;
    alignItems: Alignment;
};

export const Container = styled.div<ContainerProps>`
    font-family: ${fontFamily('default')};
    padding: ${spacing(4)};
    display: grid;
    grid-template-columns: 1fr auto;
    column-gap: ${spacing(3)};
    align-items: flex-start;
    ${modifier(
        ({ alignItems }: ContainerProps) => alignItems === 'center',
        css`
            align-items: center;
        `,
    )};
    ${variants('styleStatus', {
        success: css`
            background-color: ${color('surface.green100')};
        `,
        error: css`
            background-color: ${color('surface.red100')};
        `,
        warning: css`
            background-color: ${color('surface.yellow100')};
        `,
        info: css`
            background-color: ${color('surface.primary100')};
        `,
    })};
`;

export const Content = styled.div`
    ${typography('description')};
    color: ${color('content.primary')};
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    column-gap: ${spacing(1)};
`;

export const TopBannerLink = styled(Pressable)<PressableProps>`
    ${typography('description')}
    color: ${color('content.primary')};
    text-decoration: underline;
    min-width: fit-content;
    border: none;
    background: none;

    &:hover,
    &:visited,
    &:link,
    &:active,
    &:active:hover {
        text-decoration: underline;
        color: ${color('content.primary')};
    }
`;

TopBannerLink.displayName = 'TopBannerLink';
